

import { World, Cube } from 'tdSetup';
import TableCC from 'tdTable';
import okNoga from 'okNoga';
import ok3Dploca from 'ok3Dploca';
import okPolyTable from 'okPolyTable';
import { SwpNumber, SwpChoice } from 'swpUI';



var objects = [];
var currentObj = 0;
export let glview,gapp = 0;

document.addEventListener('DOMContentLoaded', function() {


  glview = new World();

  objects.push(new okPolyTable() );
  objects.push( new TableCC() );
  objects.push( new okNoga() );
  objects.push( new ok3Dploca() );

  buildControls();

  bindButtons();

  console.log('Initialized app');
});


function buildControls ( ) {
  

  let obj = objects[currentObj];

  glview.add( obj );
  
  let container = document.getElementById('controls');
  
  while (container.hasChildNodes()) {
    container.removeChild(container.lastChild);
  }

  for ( var param in obj.parameters ) {   
    if ( obj.parameters.hasOwnProperty(param)){
      let p = obj.parameters[param];
      if ( p.type === 'number') {
        let options = {
          label: param,
          min: p.min,
          max: p.max,
          default: p.v ,
          step: 0.01,
          screen: 0.01,
          measure: p.unit
        };
        let control = new SwpNumber ( container , options , obj );
        //console.log( p);       
      }
      if ( p.type === 'choice') {
        //et control = new SwpChoice ( p );
      }
    }
  }
}

 var nextObj = function () {

  glview.remove( objects[currentObj]);
  if ( currentObj == objects.length-1) {
    currentObj=0;
  } else {
    currentObj++;
  }
  buildControls();
}

var prevObj = function() {

  glview.remove( objects[currentObj]);
  if ( currentObj == 0) {
    currentObj=objects.length-1;
  } else {
    currentObj--;
  }
  buildControls();
}

function bindButtons () {

  var prev = document.getElementById('prevObj');
  prev.onclick = function () { prevObj()} ;

  var next = document.getElementById('nextObj');
  next.onclick = function() { nextObj() };



}

