import * as THREE from 'three';


function ParaNumber ( v , min,max, unit ){

    this.type = 'number';
    this.v = v;
    this.min = min;
    this.max = max;
    this.unit = unit || '';
    
}


let tableccDefaultParams = {
    length : new ParaNumber( 1.2, 0.6, 3.0, 'm'),
    width : new ParaNumber( 0.6, 0.4, 2.0, 'm'),
    height : new ParaNumber( 0.65, 0.2, 1.5, 'm'),
    legUpper : new ParaNumber( 0.15, 0.1, 0.5, 'm'),
    legLower: new ParaNumber( 0.03, 0.01, 0.5, 'm'),
    legAngle: new ParaNumber ( Math.PI/4 , 0 , Math.PI/2, 'rad'),
    plocaD : new ParaNumber( 0.025, 0.02, 0.1, 'm'),
    cond : function () {
        return  this.legLower.v <= this.legUpper.v && 
                this.legUpper.v * 2 < this.width.v ;
    }
}


export default function TableCC ( parameters = tableccDefaultParams ) {
    this.parameters = parameters ;

    this.group = new THREE.Group();
    this.material = new THREE.MeshStandardMaterial( { wireframe: false });

    this.extrudeSettings = { steps:1 , amount: parameters.plocaD.v , bevelEnabled: false };

    this.changed = false;

    this.build();
}

TableCC.prototype.build = function () {

    this.changed = false;

    // this.group.matrix = new THREE.Matrix4();
    // this.group.updateMatrix();

    this.group = new THREE.Group();


    let H=this.parameters.height.v;
    console.log( H);
    let W=this.parameters.width.v;
    let L=this.parameters.length.v;
    let lgU=this.parameters.legUpper.v;
    let lgD=this.parameters.legLower.v;
    let lgA=this.parameters.legAngle.v;

    // ploča 
    let cornerL = lgU * Math.cos(lgA);
    let cornerW = lgU * Math.sin(lgA);

    let pnts = [ 
        new THREE.Vector2(0,-W/2),
        new THREE.Vector2( L/2 - cornerL, -W/2 ),
        new THREE.Vector2( L/2, -W/2+cornerW),
        new THREE.Vector2( L/2, W/2 - cornerW ),
        new THREE.Vector2( L/2 - cornerL, W/2 ),
        new THREE.Vector2( -L/2 + cornerL, W/2 ),
        new THREE.Vector2( -L/2, W/2 - cornerW ), 
        new THREE.Vector2( -L/2, -W/2 + cornerW ),
        new THREE.Vector2( -L/2 + cornerL, -W/2 ),
        new THREE.Vector2(0,-W/2) 
    ];

    let ploca2d = new THREE.Shape(pnts);
    let plocaGeo = new THREE.ExtrudeBufferGeometry( ploca2d, this.extrudeSettings );
    let plocaMesh = new THREE.Mesh ( plocaGeo, this.material );

    //plocaMesh.rotateX(Math.PI/2);
    //plocaMesh.translateZ( -H);
    
    plocaMesh.castShadow = true;
    this.group.add(plocaMesh);

    let lpnts = [
        new THREE.Vector2( - lgU/2, 0),
        new THREE.Vector2(   lgU/2, 0),
        new THREE.Vector2(   lgD/2, H - this.parameters.plocaD.v),
        new THREE.Vector2( - lgD/2, H - this.parameters.plocaD.v),
        new THREE.Vector2( - lgU/2, 0)
    ];

    let leg2d = new THREE.Shape(lpnts);
    let legGeo = new THREE.ExtrudeBufferGeometry( leg2d, this.extrudeSettings );
    let legMesh = new THREE.Mesh( legGeo, this.material);

    //legMesh.rotateY( lgA );
    
    
    for ( var i=0; i<4; i++) {
        var leg = legMesh.clone( );

        if ( i == 0 || i == 2 ) {
            var dest = new THREE.Vector2( L/2 - cornerL/2, W/2 - cornerW/2 );
            if ( i == 2) {
                dest.negate();
            }
        } else {
            var dest = new THREE.Vector2( -L/2 + cornerL/2,  W/2 - cornerW/2  );
            if ( i == 3 ) {
                dest.negate();
            }
        }

        let dist = dest.length();
        dest.normalize();

        let axis = new THREE.Vector3( dest.x, dest.y , 0 );

        leg.translateOnAxis( axis , dist);
        leg.rotateX( Math.PI/2 );

        if ( i % 2 == 0 ) {
            leg.rotateY( -lgA );
        } else {
            leg.rotateY( lgA );
        }

        leg.translateY( this.parameters.plocaD.v);

        if ( i > 1 ){
            leg.translateOnAxis ( new THREE.Vector3 ( 0,0,-1) , this.parameters.plocaD.v );
        }

        leg.castShadow = true;
        this.group.add(leg);
    }
        
    this.group.matrix = new THREE.Matrix4();
    this.group.matrixWorldNeedsUpdate = true;
    this.group.updateMatrix();
    this.group.translateY(H);

    console.log( this.group);
    this.group.rotateX( Math.PI /2  );

   
}

TableCC.prototype.getMesh = function () {
    return this.group;
}

TableCC.prototype.update = function (scene) {
    this.scene = scene;
    if (this.changed) {
        scene.remove(this.group)
        this.build();       
        scene.add(this.group);
    }
}

TableCC.prototype.isOK = function ( para , v ) {
    //temporery paramterers
    let tPara = Object.assign( {} , this.parameters);
    tPara[para].v = v;
    return tPara.cond();
}